.alert-block {
  background: #ffffff;
  border-radius: 12px;
  padding: 24px;
  box-shadow:
    0 2px 8px rgba(0, 0, 0, 0.08),
    0 8px 24px rgba(0, 0, 0, 0.12);
  transition: all 0.3s ease;
  border: 1px solid #e5e7eb;
  border-left: 4px solid;
  position: relative;
  overflow: hidden;

  &:hover {
    box-shadow:
      0 4px 16px rgba(0, 0, 0, 0.15),
      0 12px 32px rgba(0, 0, 0, 0.1);
    border-color: #d1d5db;
  }
}

// Severity-based styling
.alert-block.severity-critical {
  border-left-color: #ef4444;
  background: linear-gradient(135deg, #ffffff, #fef2f2);
}

.alert-block.severity-warning {
  border-left-color: #f59e0b;
  background: linear-gradient(135deg, #ffffff, #fffbeb);
}

.alert-block.severity-info {
  border-left-color: #3b82f6;
  background: linear-gradient(135deg, #ffffff, #eff6ff);
}

.alert-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--surface-100);
}

.alert-icon-title {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  flex: 1;
}

.alert-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  transition: all 0.3s ease;

  i {
    font-size: 18px;
    font-style: normal;
  }

  &.icon-critical {
    background: linear-gradient(135deg, #ef4444, #dc2626);
    color: white;
    animation: pulse-critical 2s infinite;
  }

  &.icon-warning {
    background: linear-gradient(135deg, #f59e0b, #d97706);
    color: white;
    animation: pulse-warning 3s infinite;
  }

  &.icon-info {
    background: linear-gradient(135deg, #3b82f6, #2563eb);
    color: white;
  }
}

@keyframes pulse-critical {
  0%,
  100% {
    box-shadow: 0 0 0 0 rgba(239, 68, 68, 0.7);
  }
  50% {
    box-shadow: 0 0 0 10px rgba(239, 68, 68, 0);
  }
}

@keyframes pulse-warning {
  0%,
  100% {
    box-shadow: 0 0 0 0 rgba(245, 158, 11, 0.5);
  }
  50% {
    box-shadow: 0 0 0 8px rgba(245, 158, 11, 0);
  }
}

.alert-title-timestamp {
  flex: 1;
}

.alert-title {
  color: var(--text-color);
  font-size: 1.0625rem;
  font-weight: 700;
  margin: 0 0 4px 0;
}

.alert-timestamp {
  font-size: 0.75rem;
  color: var(--text-color-secondary);
  font-weight: 500;
}

.alert-severity-badge {
  background: var(--surface-100);
  color: var(--text-color);
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 700;
  letter-spacing: 0.5px;
  border: 1px solid var(--surface-200);

  &.severity-critical {
    background: #fef2f2;
    color: #dc2626;
    border-color: #fecaca;
  }

  &.severity-warning {
    background: #fffbeb;
    color: #d97706;
    border-color: #fed7aa;
  }

  &.severity-info {
    background: #eff6ff;
    color: #2563eb;
    border-color: #bfdbfe;
  }
}

.alert-content {
  space-y: 16px;
}

.alert-message {
  color: var(--text-color);
  line-height: 1.6;
  margin: 0 0 16px 0;
  font-size: 0.95rem;
}

.affected-entities {
  margin: 16px 0;
  padding: 16px;
  background: var(--surface-50);
  border-radius: 8px;
  border: 1px solid var(--surface-200);
}

.affected-header {
  margin-bottom: 12px;

  strong {
    color: var(--text-color);
    font-size: 0.875rem;
  }
}

.entity-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 8px;
}

.entity-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background: var(--surface-0);
  border-radius: 6px;
  border: 1px solid var(--surface-200);
  transition: all 0.2s ease;

  &:hover {
    background: var(--surface-100);
    transform: scale(1.02);
  }
}

.entity-icon {
  width: 24px;
  height: 24px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;

  &.entity-vehicle i {
    color: #3b82f6;
  }
  &.entity-driver i {
    color: #22c55e;
  }
  &.entity-geofence i {
    color: #f59e0b;
  }
}

.entity-info {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-width: 0;
}

.entity-name {
  font-weight: 600;
  color: var(--text-color);
  font-size: 0.875rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.entity-id {
  font-size: 0.75rem;
  color: var(--text-color-secondary);
  font-family: monospace;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.suggested-action {
  margin: 16px 0;
  padding: 16px;
  background: linear-gradient(135deg, #eff6ff, #dbeafe);
  border: 1px solid #bfdbfe;
  border-radius: 8px;
}

.action-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;

  .action-icon {
    font-size: 16px;
  }

  strong {
    color: var(--text-color);
    font-size: 0.875rem;
  }
}

.action-description {
  color: var(--text-color-secondary);
  line-height: 1.5;
  margin: 0 0 16px 0;
  font-size: 0.875rem;
}

.action-buttons {
  display: flex;
  gap: 8px;
}

.btn {
  padding: 8px 16px;
  border-radius: 6px;
  border: none;
  font-weight: 600;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;

  &.btn-primary {
    background: #3b82f6;
    color: white;

    &:hover {
      background: #2563eb;
      transform: translateY(-1px);
      box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
    }
  }

  &.btn-secondary {
    background: var(--surface-200);
    color: var(--text-color-secondary);

    &:hover {
      background: var(--surface-300);
      color: var(--text-color);
    }
  }
}

.source-event {
  margin-top: 16px;
  text-align: right;
}

.event-id {
  color: var(--text-color-secondary);
  font-size: 0.75rem;
  font-family: monospace;
  background: var(--surface-100);
  padding: 2px 8px;
  border-radius: 4px;
}

// Dark Mode Support
:host-context(.dark) {
  .alert-block {
    background: var(--surface-800);
  }

  .affected-entities {
    background: var(--surface-700);
    border-color: var(--surface-600);
  }

  .entity-item {
    background: var(--surface-800);
    border-color: var(--surface-600);

    &:hover {
      background: var(--surface-700);
    }
  }

  .suggested-action {
    background: linear-gradient(
      135deg,
      rgba(59, 130, 246, 0.1),
      rgba(59, 130, 246, 0.05)
    );
    border-color: rgba(59, 130, 246, 0.2);
  }

  .event-id {
    background: var(--surface-700);
  }

  .alert-severity-badge {
    &.severity-critical {
      background: rgba(239, 68, 68, 0.1);
      border-color: rgba(239, 68, 68, 0.2);
    }

    &.severity-warning {
      background: rgba(245, 158, 11, 0.1);
      border-color: rgba(245, 158, 11, 0.2);
    }

    &.severity-info {
      background: rgba(59, 130, 246, 0.1);
      border-color: rgba(59, 130, 246, 0.2);
    }
  }
}

// Responsive Design
@media (max-width: 768px) {
  .alert-block {
    padding: 16px;
  }

  .alert-header {
    flex-direction: column;
    gap: 12px;
    align-items: stretch;
  }

  .alert-severity-badge {
    align-self: flex-start;
  }

  .entity-list {
    grid-template-columns: 1fr;
  }

  .action-buttons {
    flex-direction: column;
  }
}
